import { AddLogisticRoutingModule } from './add-logistic-routing.module';
import { LogisticComponent } from './add-logistic.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/shared.module';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

@NgModule({
  imports: [
    CommonModule,
    AddLogisticRoutingModule,
    SharedModule,
    NgxDatatableModule,
    FormsModule,
    HttpModule
  ],
  declarations: [LogisticComponent]
})
export class AddLogisticModule { }



